import { IOdataFilterBuilder} from "./IOdataFilterBuilder";
import {IProperty} from "../../EntityMaps/IProperty";
import { IDateTimeOffsetFormatter} from "./IDateTimeOffsetFormatter";

export class DateOdataFilterBuilder implements IOdataFilterBuilder {
    constructor(private formatter: IDateTimeOffsetFormatter) {

    }
    getFilter = (filterData: any, property: IProperty): string => {
        let filterName = property.name.name;
        if (!filterData) {
            return "";
        }
        let value = filterData[filterName];
        if (!value) {
            return "";
        }
        let min = value.min;
        let max = value.max;

        let filterForProperty: string = '';
        if (min) {
            filterForProperty += `${filterName} ge ${this.formatter.formatLocalDate(min)}`;
        }
        if (max) {
            if (filterForProperty) {
                filterForProperty += " and ";
            }
            filterForProperty += `${filterName} le ${this.formatter.formatLocalDate(max)}`;
        }

        return filterForProperty;
    }
}


