import { Response } from "@angular/http";
import { TableColumnVm } from "./TableColumnVm";
import { Entity } from "../EntityMaps/Entity";
import { RelationService } from "../Services/RelationService";
import { RelationProperty } from "../EntityMaps/RelationProperty";
import { IProperty as Property } from "../EntityMaps/IProperty";
import * as RequestService from "./TableRequestService";
import {TableRequest, TableDataMode } from "../ResponsiveTable/table-request";
import {DataService} from "../Services/data.service";
import {ListResult} from "../ResponsiveTable/list-result";
import {SortDirection} from "../ResponsiveTable/SortDirection";

export enum TableViewMode {
	Regular,
	Stack,
	Toggle
}

export class TableVm {
	columns: TableColumnVm[] = [];
	currentPage = 1;
	pageSize = 10;
	total: number;
	keyword = "";
	options = new TableRequest();
	filterData: any = {};
	linkModeFilterData: any = {};
	tableData: ListResult;
	isLoading = true;
	viewMode = TableViewMode.Stack;
    visibleColumns: string[] = [];

	constructor(
		private dataService: DataService,
		public entity: Entity,
		private relationService: RelationService,
		private tableRequestService: RequestService.TableRequestService
	) {

		for (let property of this.entity.properties) {
			if (property instanceof RelationProperty) {
				if (!this.relationService.isOneToManyOrManyToManyProperty(property)) {
					this.addColumn(property);
				}
			} else {
				this.addColumn(property);
			}
		}
	}

	addColumn = (property: Property) => {
		const column = new TableColumnVm();
		column.property = property;
		column.table = this;
		this.columns.push(column);
	}

	private _sortColumn: TableColumnVm;
	set sortColumn(column: TableColumnVm) {
		this._sortColumn = column;
		this.columns.forEach(c => {
			if (c.property.name.name === column.property.name.name) {
				c.sortDirection = column.sortDirection;
			} else {
				c.sortDirection = SortDirection.Unsorted;
			}
		});
	}

	get sortColumn(): TableColumnVm {
		return this._sortColumn;
	}
	getItemOffset = () => {
		let offset = (this.currentPage - 1) * this.pageSize;
		if (offset < 0) {
			offset = 0;
		}
		return offset;
	};
	get pageCount() {
		return Math.ceil(this.total / this.pageSize);
	};
	getTableOptions = () => {
		return this.createTableOptionsForFilterData(this.filterData, this.options);
	};

	createTableOptionsForFilterData = (filterData: any, newRequest: TableRequest) => {
		let request = new TableRequest();

		request.limit = this.pageSize;
		request.offset = this.getItemOffset();
		request.search = this.keyword;
		request.tableDataMode = newRequest.tableDataMode;
        request.queryName = newRequest.queryName;
	    if (newRequest.tableDataMode === TableDataMode.Link) {
	        request.parentProperty = this.options.parentProperty;
	        request.parentId = this.options.parentId;
	    }

	    this.setSortColumnToRequest(request);
		if (this.filterData) {
			this.tableRequestService.setFilterDataToRequest(filterData, request);
		}
		return request;
	};


	private setSortColumnToRequest = (request: TableRequest) => {
		if (this.sortColumn) {
			request.sortDirection = this.sortColumn.sortDirection;
			request.sortColumn = this.sortColumn.property.name.name;
		}
	};
	getLinkTableOptions = () => {
		return this.createTableOptionsForFilterData(this.linkModeFilterData, this.options);
	};
	downloadItems = (entity: Entity = null, options: TableRequest = null) => {
		if (!entity) {
			entity = this.entity;
		}
		if (!options) {
			options = this.getTableOptions();
        } else {
		    let newOptions = this.getTableOptions();
            options.parentProperty = newOptions.parentProperty;
		    options.parentId = newOptions.parentId;
		}
		this.isLoading = true;

		this.downloadFromDataService(entity, options);
	};


	public downloadFromDataService = (entity: Entity, options: TableRequest) => {
		this.dataService.getEntities(entity, options)
			.map((res: Response) => {
				return res.json();
			})
			.subscribe(this.acceptData,
			this.downloadError,
			this.downloadComplete
			);
	}
	private downloadError=(err: any) => {
		console.log(err);
	}
	refresh = () => {
		this.downloadItems(this.entity, this.getTableOptions());
	}
	delete = (item: any) => {
		return this.dataService.delete(this.entity, [item[this.entity.idColumn.name.name]]);
	}

	link = (item: any, parentProperty: string, parentId: number) => {
		return this.dataService.link(this.entity, parentProperty, parentId, [item[this.entity.idColumn.name.name]]);
	}

	unlink = (item: any, parentProperty: string, parentId: number) => {
		return this.dataService.unlink(this.entity, parentProperty, parentId, [item[this.entity.idColumn.name.name]]);
	}

	acceptData = (data: ListResult) => {
		this.tableData = data;
		this.total = data["@odata.count"];
	};

	downloadComplete = () => {
		this.isLoading = false;
	};
	toggleVisibility = (column: TableColumnVm) => {
		column.isVisible = !column.isVisible;
	}
}


