import {EntityName} from "./EntityName";
import {IProperty } from "./IProperty";

export class Entity {
	constructor(
		public name: EntityName,
		public pluralName: string,
		public idColumn: IProperty
	) {
		this.canDelete = true;
		this.canEdit = true;
		this.canAdd = true;
	}
	properties: IProperty[];
	canDelete: boolean;
	canEdit: boolean;
	canAdd: boolean;
}

 


