import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
    selector: 'rd-table-pagination',
    templateUrl: 'pagination.component.html'
})
export class PaginationComponent {
    @Input() current: number = 1;
    @Input() totalPageCount: number = 1;
    @Input() maxButtonCount: number = 5;

    @Output() pageSelected = new EventEmitter();

    getCurrentPage = () => {
        if (this.current < 1) {
            return 1;
        } else if (this.current > this.totalPageCount) {
            return this.totalPageCount;
        } else {
            return this.current;
        }
    };

    isPreviousPageEnabled = () => {
        return this.getCurrentPage() > 1;
    };

    isNextPageEnabled = () => {
        return this.getCurrentPage() < this.totalPageCount;
    };

    isFirstPageEnabled = () => {
        return this.getCurrentPage() > 1;
    };

    isLastPageEnabled = () => {
        return this.getCurrentPage() < this.totalPageCount;
    };

    getPreviousPage(): number {
        if (this.getCurrentPage() > 1) {
            return this.getCurrentPage() - 1;
        }
        return 1;
    };

    getNextPage(): number {
        if (this.getCurrentPage() < this.totalPageCount) {
            return this.getCurrentPage() + 1;
        } else {
            return this.totalPageCount;
        }
    };

    getFirstShownPage = () => {
        if (this.totalPageCount <= this.maxButtonCount) {
            return 1;
        } else {
            let firstShownPage = this.getCurrentPage() - Math.floor(this.maxButtonCount / 2);
            if (this.totalPageCount - firstShownPage < this.maxButtonCount) {
                firstShownPage = this.totalPageCount - this.maxButtonCount + 1;
            }
            firstShownPage = Math.max(firstShownPage, 1);
            return firstShownPage;
        }
    };

    getLastShownPage = () => {
        if (this.totalPageCount <= this.maxButtonCount) {
            return Math.min(this.maxButtonCount, this.totalPageCount);
        } else {
            return Math.min(this.getFirstShownPage() + this.maxButtonCount - 1, this.totalPageCount);
        }
    };

    selectPage(pageNumber: number = 1) {
        const previousPage = this.current;
        this.current = pageNumber;
        this.current = this.getCurrentPage();
        if (previousPage != this.current) {
            this.pageSelected.emit(pageNumber);
        }
    };

    getPageNumbers = () => {
        let pages: number[] = [];
        for (var page = this.getFirstShownPage(); page <= this.getLastShownPage(); page++) {
            pages.push(page);
        }
        return pages;
    };

    getNumber(length: number) {
        return new Array(length);
    };
}



