import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { HttpModule, JsonpModule } from '@angular/http';


import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { ListPage } from '../pages/list/list';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { EntityListPage } from "../pages/entityList/entity-list";
import { EntityMap } from "../EntityMaps/EntityMap";
import { EntityDetailsPage } from "../pages/entityDetails/entity-details";
import { ItemListPage } from "../pages/itemList/item-list";
import { DataService } from "../Services/data.service";
import { ODataUrlBuilder } from "../Services/OData/ODataUrlBuilder";
import { ODataFilterService } from "../Services/OData/ODataFilterService";
import { TableRequestService } from "../ViewModels/TableRequestService";
import { ODataFilterBuilderFactory } from "../Services/OData/ODataFilterBuilderFactory";
import { ManyToManyRelationODataFilterBuilder } from "../Services/OData/ManyToManyRelationODataFilterBuilder";
import { ManyToOneRelationODataFilterBuilder } from "../Services/OData/ManyToOneRelationODataFilterBuilder";
import { LinkModeODataParameterBuilder } from "../Services/OData/LinkModeODataParameterBuilder";
import { ParentNameOdataParameterBuilder } from "../Services/OData/ParentNameOdataParameterBuilder";
import { EntityPropertyService } from "../Services/EntityPropertyService";
import { ParentNameExpandParameterService } from "../Services/OData/ParentNameExpandParameterService";
import { NtoNChildrenLinkModeParameterBuilder } from "../Services/OData/NtoNChildrenLinkModeParameterBuilder";
import { ExpandParameterBuilder } from "../Services/OData/ExpandParameterBuilder";
import { OdataLinkRequestService } from "../Services/OData/OdataLinkRequestService";
import { RelationService } from "../Services/RelationService";
import { TableComponent } from "../ResponsiveTable/table.component";
import { SearchPropertyComponent } from "../ResponsiveTable/search-property.component";
import { FormControlService } from "../ResponsiveTable/FormControlService";
import { TableHeaderCellComponent } from "../ResponsiveTable/table-header-cell.component";
import { ViewPropertyComponent } from "../ResponsiveTable/view-property.component";
import { ModalDialogComponent } from "../ResponsiveTable/modal-dialog.component";
import { PaginationComponent } from "../ResponsiveTable/pagination.component";
import { EditEntityComponent } from "../ResponsiveTable/edit-entity.component";
import { EditPropertyComponent } from "../ResponsiveTable/edit-property.component";
import { ImageDisplayComponent } from "../ResponsiveTable/image-display.component";
import { TableHeaderComponent } from "../ResponsiveTable/table-header.component";
import { ViewEntityComponent } from "../ResponsiveTable/view-entity.component";
import { ResponsiveState, RESPONSIVE_DIRECTIVES } from 'responsive-directives-angular2';
import {GuidService} from "../Commons/GuidService";
import {EntityService} from "../Services/EntityService";

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ListPage,
    EntityListPage,
    EntityDetailsPage,
    ItemListPage,
    TableComponent,
    SearchPropertyComponent,
    TableHeaderCellComponent,
    ViewPropertyComponent,
    ModalDialogComponent,
    PaginationComponent,
    EditEntityComponent,
    EditPropertyComponent,
    ImageDisplayComponent,
    TableHeaderComponent,
    ViewEntityComponent,
    RESPONSIVE_DIRECTIVES
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {},
      {
        links: [
          { component: HomePage, name: 'Home', segment: 'home' },
         // { component: ListPage, name: 'List', segment: 'List' },
          { component: EntityListPage, name: "Entities", segment: ":app/Entities" },
          { component: EntityDetailsPage, name: 'Details', segment: ':app/:entityName/:itemId' },
          { component: ItemListPage, name: "ItemList", segment: ":app/:entityName/List" }

        ]
      }),
    HttpModule,
    JsonpModule

  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ListPage,
    EntityListPage,
    EntityDetailsPage,
    ItemListPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    EntityMap,
    DataService,
    ODataUrlBuilder,
    ODataFilterService,
    TableRequestService,
    ODataFilterBuilderFactory,
    ManyToManyRelationODataFilterBuilder,
    ManyToOneRelationODataFilterBuilder,
    LinkModeODataParameterBuilder,
    ParentNameOdataParameterBuilder,
    EntityPropertyService,
    ParentNameExpandParameterService,
    NtoNChildrenLinkModeParameterBuilder,
    ExpandParameterBuilder,
    OdataLinkRequestService,
    RelationService,
    FormControlService,
    ResponsiveState,
    GuidService,
    EntityService
  ]
})
export class AppModule { }



