
import {IProperty} from "./IProperty";
import {PropertyName } from "./PropertyName";
import {EntityName} from "./EntityName";
import {RelationMultiplicity } from "./RelationMultiplicity";
import {PropertyType } from "./PropertyType";

export class RelationProperty implements IProperty {
	constructor(public name: PropertyName,
		public displayPropertyName: PropertyName,
		public entityName: EntityName,
		public entityPluralName: string,
        public thisEndMultiplicity: RelationMultiplicity,
        public thisEndObjectPropertyName:string,
		public thatEndEntityName: string,
        public thatEndPropertyName: string,
		public labelText: string = name.name,
		public type: PropertyType = PropertyType.Relation,
		public canSearch: boolean = true,
		public canEdit: boolean = true,
		public canSort: boolean = true,
		public required: boolean = true
	) {

	}

}



