import { FormControl} from '@angular/forms';

export class NumberValidator {
	public static max(value: number) {
		return (c: FormControl) => {
			if (c.value > value) {
				return {
					maxValue: false
				};
			} else {
				return null;
			}
		}
	}

	public static min(value: number) {
		return (c: FormControl) => {
			if (c.value < value) {
				return {
					minValue: false
				};
			} else {
				return null;
			}
		}
	}
}


