import { Injectable } from '@angular/core';
import { EntityMap } from "../../EntityMaps/EntityMap";
import { IOdataFilterBuilder } from "./IOdataFilterBuilder";
import { RelationProperty } from "../../EntityMaps/RelationProperty";
import { Property } from "../../EntityMaps/Property";

@Injectable()
export class ManyToManyRelationODataFilterBuilder implements IOdataFilterBuilder {

    constructor(private entityMap: EntityMap) { }

    getFilter = (filterData: any, property: Property): string => {
        let relationProperty = <RelationProperty>property;
        let filterName = property.name.name;
        let value = filterData[filterName];
        //http://localhost:2244/odata/Tags/?$count=true&$top=10&$skip=0&$filter=Products/any(product:%20product/ProductId%20eq%201)&$select=Id,Name,CreatedBy
        let thatEndProperty = this.entityMap.getThatEndProperty(relationProperty);
        let thisEndEntity = this.entityMap.getEntity(thatEndProperty.thatEndEntityName);
        let entity = this.entityMap.getEntity(relationProperty.entityName.name);

        let selectProperties = thisEndEntity.idColumn.name.name;
        for (var thisEndProperty of thisEndEntity.properties) {
            if (thisEndProperty.name.name) {
                selectProperties += "," + thisEndProperty.name.name;
            }
        }

        //Wrong: http://localhost:2244/odata/Tags/?$count=true&$top=10&$skip=0&$filter=Products/any(e:%20e/ProductID%20eq%201)&$select=ProductID,ProductName,SupplierID,CategoryID,Tags,ManufactureDate,UnitPrice,CreatedBy
        let filterForProperty = `${relationProperty.thisEndObjectPropertyName}/any(e: e/${entity.idColumn.name.name} eq ${value})&$select=${selectProperties}`;
        return filterForProperty;
    }
}


