import { Component } from '@angular/core';
import { NavController, NavParams, AlertController } from 'ionic-angular';

import { EntityDetailsPage } from "../entityDetails/entity-details";
import { TableVm } from "../../ViewModels/TableVm";
import { Entity } from "../../EntityMaps/Entity";
import { DataService } from "../../Services/data.service";
import { RelationService } from "../../Services/RelationService";
import { TableRequestService } from "../../ViewModels/TableRequestService";
import { EntityMap } from "../../EntityMaps/EntityMap";
import { EntityService } from "../../Services/EntityService";

@Component({
  selector: 'item-list',
  templateUrl: 'item-list.html'
})
export class ItemListPage {
  entity: Entity;
  icons: string[];
  table: TableVm;
  entityName:string;
  constructor(public navCtrl: NavController,
    private dataService: DataService,
    private relationService: RelationService,
    private tableRequestService: TableRequestService,
    public navParams: NavParams,
    private entityMap: EntityMap,
    private alertCtrl: AlertController,
    private entityService: EntityService
  ) {
    // If we navigated to this page, we will have an item available as a nav param
    this.entity = navParams.get('entity');
    if (!this.entity) {
      this.entityName = navParams.get('entityName');
      if (this.entityMap.getEntities().length===0) {
        this.entityMap.addCompletedSubscribers(this.entityMapLoadCompleted);
      }
    } else {
      this.entityName = this.entity.name.name;
      this.loadItems();
    }
  }
  private entityMapLoadCompleted = () => {
    this.entity = this.entityService.getEntityByName(this.entityName);
    this.loadItems();
  }
  private loadItems=() => {
        this.createTableVm(this.entity);
    for (var property of this.entity.properties) {
      this.table.visibleColumns.push(property.name.name);
    }
  }
  createTableVm(entity: Entity) {
    if (!this.table) {
      let newTable = new TableVm(this.dataService, this.entity, this.relationService, this.tableRequestService);
      this.table = newTable;
    } else {
      if (this.table.entity.name.name !== this.entity.name.name) {
        let newTable = new TableVm(this.dataService, this.entity, this.relationService, this.tableRequestService);
        this.table = newTable;
        this.table.refresh();
      }
    }

  }
  itemTapped(event, item) {
    // That's right, we're pushing to ourselves!
    this.navCtrl.push(EntityDetailsPage, {
      item: item,
      app: "MyApp",
      entityName: this.entity.name.name,
      itemId: "ItemId0"

    });
  }
  addNewItem = () => {
    console.log("addNewItem");
  }



  showSelectColumnCheckbox() {
    let alert = this.alertCtrl.create();
    alert.setTitle('Select Columns');

    for (var property of this.entity.properties) {
      alert.addInput({
        type: 'checkbox',
        label: property.labelText,
        value: property.name.name,
        checked: this.table.visibleColumns.indexOf(property.name.name) >= 0
      });
    }

    alert.addButton('Cancel');
    alert.addButton({
      text: 'OK',
      handler: (data: string[]) => {
        this.table.visibleColumns.length = 0;
        for (let columnName of data) {
          this.table.visibleColumns.push(columnName);
        }
        console.log('Checkbox data:', data);
      }
    });
    alert.present();
  }
}



