import { Component, Input, OnInit } from '@angular/core';
import { IProperty } from "../EntityMaps/IProperty";
import { Entity } from "../EntityMaps/Entity";
import { EntityMap } from "../EntityMaps/EntityMap";
import { RelationProperty } from "../EntityMaps/RelationProperty";
import { NavController } from 'ionic-angular';
import { EntityDetailsPage } from "../pages/entityDetails/entity-details";

@Component({
  selector: 'rd-view-property',
  templateUrl: 'view-property.component.html',
  styleUrls: ['']

})
export class ViewPropertyComponent implements OnInit {

  @Input()
  property: IProperty;
  @Input()
  entity: Entity;
  @Input()
  item: any = {};
  sub: any;

  constructor(
    private entityMap: EntityMap,
    private navCtrl: NavController
  ) {
  }

  ngOnInit() { }

  get value() {
    return this.item ? this.item[this.property.name.name] : null;
  }

  get detailsUrl() {
    return '/' + this.entity.name.name + "/view/" + this.item[this.entity.idColumn.name.name];
  }

  showDetailPage = (event, entity): void => {
    this.navCtrl.push(EntityDetailsPage,
      {
        entity: this.entity,
        item: this.item,
        app: this.entityMap.app.name,
        entityName: this.entity.name.name,
        itemId: "ItemId0"

      });
    return;
  };

  get parentUrl() {
    let parentProperty = <RelationProperty>this.property;
    return '/' + parentProperty.entityName.name + "/view/" + this.item[this.property.name.name];
  };

  get parentText() {
    let parentProperty = <RelationProperty>this.property;
    let parentEntity = this.entityMap.getEntity(parentProperty.entityName.name);
    let parentDescriptionProperty = parentEntity.properties[1];
    if (parentDescriptionProperty) {
      let parentItem = this.item[parentProperty.entityName.name];
      if (parentItem) {
        return [parentDescriptionProperty.name.name];
      }
    }
    return null;
    //return this.item[parentProperty.displayPropertyName.name];
  }
}


