import * as OdataFilterBuilder from "./IOdataFilterBuilder";
import {IProperty as Property} from "../../EntityMaps/IProperty";

export class StringOdataFilterBuilder implements OdataFilterBuilder.IOdataFilterBuilder {
    getFilter = (filterData: any, property: Property): string => {
        let filterName = property.name.name;
        let value = filterData[filterName];
        let filterForProperty: string = `contains(${filterName}, '${value}')`;
        return filterForProperty;
    }
}


