import { Component, EventEmitter, Input, Output, OnInit, ChangeDetectorRef } from '@angular/core';

import { Response } from '@angular/http';
import { FormBuilder,
	FormGroup,
	Validators,
	ValidatorFn
} from '@angular/forms';
import {Entity} from "../EntityMaps/Entity";
import {DataService} from "../Services/data.service";
import {EntityMap} from "../EntityMaps/EntityMap";
import {RelationService} from "../Services/RelationService";
import {TableRequestService} from "../ViewModels/TableRequestService";
import {PropertyType} from "../EntityMaps/PropertyType";
import {RelationProperty} from "../EntityMaps/RelationProperty";
import {Property} from "../EntityMaps/Property";
import {TableVm} from "../ViewModels/TableVm";
import {NumberValidator} from "../Validators/number.validators";
declare var jQuery: any;
@Component({
	selector: 'rd-view-entity',
	templateUrl: 'view-entity.component.html'
})
export class ViewEntityComponent implements OnInit {
	sub: any;

	@Input() entity: Entity;
	@Input() item: any = {};
	@Output() eventName = new EventEmitter();
	form: FormGroup;
	childTabs: ResponsiveTab[];
	activeTabIndex: number;
	id: number;
	constructor(
		private dataService: DataService,
		private entityMap: EntityMap,
		private cdr: ChangeDetectorRef,
		private fb: FormBuilder,
		private relationService: RelationService,
		private tableRequestService: TableRequestService
	) {
	}

	routeParameterChanged = (params: any) => {
		const entityNameParam = 'entityName';
		const idParam = 'id';
		jQuery('#tabHeader_details').click();
		if (params[entityNameParam] !== undefined) {
			let entityName = params[entityNameParam];
			this.id = params[idParam];
			this.entity = this.entityMap.getEntity(entityName);
			this.setChildProperties(this.entity);
			this.createFormControls();

			this.dataService.getEntity(this.entity, this.id)
				.map((res: Response) => {
					return res.json();
				})
				.subscribe(this.acceptData,
				(err: any) => console.log(err),
				this.downloadComplete
				);
		}
	};

    ngOnInit() {
        //TODO
		//this.sub = this.route.params.subscribe(this.routeParameterChanged);
		this.cdr.detectChanges();
		jQuery('#viewDetailsTabs').tabCollapse();
	}
	is1ToNProperty(property: Property) {
		return property.type === PropertyType.Relation &&
			this.relationService.is1toNRelation((<RelationProperty>property));
	}
	isManyToOneProperty(property: Property) {
		return property.type === PropertyType.Relation &&
			this.relationService.isManyToOneProperty((<RelationProperty>property));
	}

	private setChildProperties = (entity: Entity) => {
		let childTabs: ResponsiveTab[] = [];
		for (let property of entity.properties) {
			if (property.type === PropertyType.Relation) {
				let relationProperty = <RelationProperty>property;
				let isChild = this.relationService.isOneToManyOrManyToManyProperty(relationProperty);
				if (isChild) {
					const tab = new ResponsiveTab();
					tab.childProperty = relationProperty;
					tab.anchor = "tab_" + relationProperty.name.name;
					let relationEntity = this.entityMap.getEntity(relationProperty.entityName.name);
                    tab.headerText = relationProperty.name.name;
					tab.relationEntity = relationEntity;
					childTabs.push(tab);
				}
			}
		}
		this.childTabs = childTabs;
	};
	showChildrenTable = (tab: ResponsiveTab) => {
		if (!tab.table) {
			let table = new TableVm(this.dataService, tab.relationEntity, this.relationService, this.tableRequestService);
			table.filterData[tab.childProperty.thatEndPropertyName] = this.id;
			tab.table = table;
		}
	};

	private createFormControls = () => {
		let controls: any = {};

		for (let property of this.entity.properties) {
			let control: any[] = [''];

			let validators: ValidatorFn[] = [];

			if (property.required) {
				validators.push(Validators.required);
			}

			if (property.pattern) {
				validators.push(Validators.pattern(property.pattern));
			}
			switch (property.type) {
				case PropertyType.String:
					if (property.maxValue) {
						validators.push(Validators.maxLength(<number>property.maxValue));
					}
					if (property.minValue) {
						validators.push(Validators.maxLength(<number>property.minValue));
					}
					break;

				case PropertyType.Number:
					if (property.maxValue) {
						validators.push(NumberValidator.max(<number>property.maxValue));
					}
					if (property.minValue) {
						validators.push(NumberValidator.max(<number>property.minValue));
					}
					break;

				default:
			}

			control.push(validators);
			controls[property.name.name] = control;
		}

		this.form = this.fb.group(controls);
	};

	acceptData = (data: any) => {
		this.item = data;
		this.cdr.detectChanges();

	};

	downloadComplete = () => {
	};

	acceptSubmitResult = (result: any) => {

	};

	submitEntity = () => {
		this.dataService.save(this.entity, this.item)
			.map((res: Response) => {
				return res.json();
			})
			.subscribe(this.acceptSubmitResult,
			(err: any) => console.log(err),
			this.downloadComplete
			);
	};

	get editUrl() {
		return '/' + this.entity.name.name + "/edit/" + this.item[this.entity.idColumn.name.name];
	}
	get listUrl() {
		return '/' + this.entity.name.name + "/list/";
	}
	isFormValid = () => {

	};
}

export class ResponsiveTab {
	anchor: string;
	headerText: string;
	active: boolean;
	childProperty: RelationProperty;
	table: TableVm;
	relationEntity: Entity;
	index: number;
}


