import 'rxjs/Rx';
import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { LoadingController } from 'ionic-angular';

import { Entity } from "./Entity";
import { RelationProperty } from "./RelationProperty";

export class App {
  name: string = "MyApp";
}
export interface CompletedCallback {
  (): void;
}
@Injectable()
export class EntityMap {
  loaded = false;
  private loader: any;
  app: App = new App();
  private completedSubscribers: CompletedCallback[] = [];
  constructor(private http: Http,
    public loadingCtrl: LoadingController
  ) {

  }
  private entities: Entity[] = [];
  loadEntities = () => {
    console.log("EntityMap.loadEntities");
    console.trace();
    if (!this.loaded) {
      this.loaded = false;
      this.showLoading();
      this.http.get("http://localhost:2244/api/entity/Get")
        .map((res: Response) => {
          return res.json();
        })
        .subscribe(this.acceptData,
        this.downloadError,
        this.downloadComplete
        );
    }
  }

  addCompletedSubscribers = (callback: CompletedCallback) => {
    this.completedSubscribers.push(callback);
  }
  emptyCompletedSubscribers = () => {
    this.completedSubscribers.length = 0;
  }
  showLoading = () => {
    if (this.entities.length === 0) {
      this.loader = this.loadingCtrl.create({
        content: 'Loading...',
        dismissOnPageChange: false
      });
      this.loader.present();
    }
  };
  downloadError = (err: any) => {
    console.log(err);
  }
  acceptData = (entityList: Entity[]) => {
    console.log("EntityMap.acceptData");

    this.entities.length = 0;
    for (var item of entityList) {
      this.entities.push(item);
    }
    this.loaded = true;
    this.loader.dismiss();
  };

  downloadComplete = () => {
    for (var callback of this.completedSubscribers) {
      callback();
    }
  };
  getEntities = () => {
    //this.loadEntities();
    return this.entities;
  }
  getEntity(entityName: string) {
    let entities = this.getEntities();
    for (var entity of entities) {
      if (entity.name.name === entityName) {
        return entity;
      }
    }
    return null;
  }

  getThatEndProperty = (relationProperty: RelationProperty) => {
    let thatEndEntity = this.getEntity(relationProperty.thatEndEntityName);

    let thatEndProperty: RelationProperty = null;
    for (var thatProperty of thatEndEntity.properties) {
      if (thatProperty.name.name === relationProperty.thatEndPropertyName) {
        thatEndProperty = <RelationProperty>thatProperty;
      }
    }
    return thatEndProperty;
  };
}


