import { Injectable } from '@angular/core';
import { RelationProperty } from "../EntityMaps/RelationProperty";
import { RelationMultiplicity } from "../EntityMaps/RelationMultiplicity";
import { EntityMap } from "../EntityMaps/EntityMap";

@Injectable()
export class RelationService {
  constructor(private entityMap: EntityMap) {
  }
  isOneToManyOrManyToManyProperty = (relationProperty: RelationProperty) => {
    let isOnetoMany = this.is1toNRelation(relationProperty);
    let isNtoN = this.isNtoNRelation(relationProperty);
    let isChild = isOnetoMany || isNtoN;
    return isChild;
  }

  is1toNRelation = (relationProperty: RelationProperty) => {
    let isThisEnd1 = relationProperty.thisEndMultiplicity === RelationMultiplicity.One ||
      relationProperty.thisEndMultiplicity === RelationMultiplicity.ZeroOrOne;
    let thatEndProperty = this.entityMap.getThatEndProperty(relationProperty);
    if (!thatEndProperty) {
      return true;
    }
    let isThatEndMany = this.isManyRelantion(thatEndProperty.thisEndMultiplicity);
    let is1toMany = isThisEnd1 && isThatEndMany;
    return is1toMany;
  };

  isManyToOneProperty = (relationProperty: RelationProperty) => {
    let isThisEndMany = this.isManyRelantion(relationProperty.thisEndMultiplicity);
    let thatEndProperty = this.entityMap.getThatEndProperty(relationProperty);
    if (!thatEndProperty) {
      return true;
    }
    let isThatEndOne = this.isOneRelantion(thatEndProperty.thisEndMultiplicity);
    let isManyToOne = isThisEndMany && isThatEndOne;
    return isManyToOne;
  };

  isManyRelantion = (multiplicity: RelationMultiplicity) => {
    return multiplicity === RelationMultiplicity.ZeroOrMore ||
      multiplicity === RelationMultiplicity.OneOrMore;
  };
  isOneRelantion = (multiplicity: RelationMultiplicity) => {
    return multiplicity === RelationMultiplicity.One ||
      multiplicity === RelationMultiplicity.ZeroOrOne;
  };


  isNtoNRelation = (relationProperty: RelationProperty) => {

    let thatEndProperty = this.entityMap.getThatEndProperty(relationProperty);
    if (!thatEndProperty) {
      return false;
    }
    let isThisEndMany = this.isManyRelantion(relationProperty.thisEndMultiplicity);

    let isThatEndMany = this.isManyRelantion(thatEndProperty.thisEndMultiplicity);
    let isNtoN = isThisEndMany && (thatEndProperty) && isThatEndMany;

    return isNtoN;
  };
}


