import * as OdataFilterBuilder from "./IOdataFilterBuilder";
import {IProperty} from "../../EntityMaps/IProperty";

export class NumberOdataFilterBuilder implements OdataFilterBuilder.IOdataFilterBuilder {
    getFilter = (filterData: any, property: IProperty): string => {
        let filterName = property.name.name;
        if (!filterData) {
            return "";
        }
        let value = filterData[filterName];
        if (!value) {
            return "";
        }
        let min = value.min;
        let max = value.max;

        let filterForProperty: string = '';
        if (min) {
            filterForProperty += `${filterName} ge ${min}`;
        }
        if (max) {
            if (filterForProperty) {
                filterForProperty += " and ";
            }
            filterForProperty += `${filterName} le ${max}`;
        }

        return filterForProperty;
    }
}


