import { Component } from '@angular/core';

import { NavController, NavParams, LoadingController } from 'ionic-angular';


import { EntityMap } from "../../EntityMaps/EntityMap";
import { Entity } from "../../EntityMaps/Entity";
import { ItemListPage } from "../itemList/item-list";

@Component({
  selector: 'entity-list',
  templateUrl: 'entity-list.html'
})
export class EntityListPage {
  icons: string[];
  items: Array<{ entity: Entity, icon: string }>;
  entities: Entity[];

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private entityMap: EntityMap,
    public loadingCtrl: LoadingController
  ) {
    // If we navigated to this page, we will have an item available as a nav param

    // Let's populate this page with some filler content for funzies
    this.icons = ['flask', 'wifi', 'beer', 'football', 'basketball', 'paper-plane',
      'american-football', 'boat', 'bluetooth', 'build'];
    if (this.entityMap.getEntities().length === 0) {
      this.entityMap.addCompletedSubscribers(this.entityMapLoadCompleted);
    } else {
      this.loadEntities();
    }

  }
  private entityMapLoadCompleted = () => {
    this.loadEntities();
  }
  itemTapped = (event, item) => {
    // That's right, we're pushing to ourselves!
    this.navCtrl.push(ItemListPage, {
      app: this.entityMap.app.name,
      entity: item.entity,
      entityName: item.entity.name.name
    });
  };
  loadEntities = () => {
    console.log("EntityListPage.loadEntities");
    console.trace();

    this.entities = this.entityMap.getEntities();

    this.items = [];
    for (var entity of this.entities) {
      this.items.push({
        entity: entity,
        icon: this.icons[Math.floor(Math.random() * this.icons.length)]
      });
    }
  };




}



