import { Component, EventEmitter, Input, Output } from '@angular/core';

declare var jQuery: any;

@Component({
    selector: 'rd-modal-dialog',
    templateUrl: 'modal-dialog.component.html'
})
export class ModalDialogComponent {
    @Input() title: string;
    @Input() message: string;
    @Input() buttonIconClass: string;
    @Input() item: any;
    @Input() itemId:string|number;

    @Output() cancelClicked = new EventEmitter();
    @Output() okClicked = new EventEmitter();

    get dialogId(): string {
        return "dialog_" + this.itemId;
    }

    clickOk = () => {
        this.okClicked.emit(this.item);
    };
    clickCancel = () => {
        this.cancelClicked.emit(this.item);
    };

    open = () => {
        jQuery("#" + this.dialogId).modal('show');
    };
}



