import { Injectable } from '@angular/core';
import { IOdataFilterBuilder } from "./IOdataFilterBuilder";
import { Property } from "../../EntityMaps/Property";

@Injectable()
export class ManyToOneRelationODataFilterBuilder implements IOdataFilterBuilder {
    getFilter = (filterData: any, property: Property): string => {
        let filterName = property.name.name;
        let value = filterData[filterName];
        return `${property.name.name} eq ${value}`;
    }
}


