import {SortDirection} from "./SortDirection";

export class TableRequest {
    offset: number;
    limit: number;
    sortColumn: string;
    sortDirection: SortDirection;
    search: string;
    filterData: any;
	linkModeFilterData: any;
	queryName: string = "GetPageOfAll";
	tableDataMode:TableDataMode= TableDataMode.Normal;
	parentProperty: string;
	parentId: number;
}
export enum TableDataMode {
	Normal,
	Link
}


