import { Injectable } from "@angular/core";
import {TableRequest, TableDataMode } from "../ResponsiveTable/table-request";

@Injectable()
export class TableRequestService {
	setFilterDataToRequest = (filterData: any, request: TableRequest) => {
		if (!request.filterData) {
			request.filterData = {};
		}
		for (var filterName in filterData) {
			if (Object.prototype.hasOwnProperty.call(filterData, filterName)) {
				var filterValue = filterData[filterName];
				request.filterData[filterName] = filterValue;
			}
		}
        if (request.tableDataMode === TableDataMode.Link) {
            let parentId = this.getParentId(request);
            if (parentId) {
                request.parentId = parentId;
            }
			
			delete request.filterData[request.parentProperty];
		}
		
	};


	private getParentId = (request: TableRequest) => {
		return request.filterData[request.parentProperty];
	}
}


