import { Component, EventEmitter, Input, Output, OnInit } from '@angular/core';

import {SortDirection} from "./SortDirection";
import {TableColumnVm} from "../ViewModels/TableColumnVm";

@Component({
    selector: 'table-header-cell',
    templateUrl: 'table-header-cell.component.html',
    styles: [`.sortable tr {
    cursor: pointer;
}`]
})
export class TableHeaderCellComponent implements OnInit {
    @Input() column: TableColumnVm;

    @Output() sorted = new EventEmitter();

    @Input() ascClass: string = "arrow-up";
    @Input() descClass: string = "arrow-down";
    @Input() unsortedClass: string = "fa-sort";

    get sortClass() {
        if (this.column.property.canSort) {
            if (this.column.sortDirection === SortDirection.Asc) {
                return this.ascClass;
            } else if (this.column.sortDirection === SortDirection.Desc) {
                return this.descClass;
            } else {
                return this.unsortedClass;
            }
        } else {
            return '';
        }
    }

    public get sortableClass(): string {
        if (this.column.property.canSort) {
            return " sortable ";
        } else {
            return '';
        }
    }

    toggleSort = () => {
        if (this.column.property.canSort) {
            if (this.column.sortDirection === SortDirection.Asc) {
                this.column.sortDirection = SortDirection.Desc;
            } else {
                this.column.sortDirection = SortDirection.Asc;
            }
            this.sorted.emit(this.column);
        }
    };

    resetSortToUnsort = () => {
        if (this.column.property.canSort) {
            this.column.sortDirection = SortDirection.Unsorted;
        }
    };

    ngOnInit() { }
}


