import { Component, Input, OnInit } from '@angular/core';
import {IProperty as Property} from "../EntityMaps/IProperty";
import {Entity} from "../EntityMaps/Entity";
import {EntityMap} from "../EntityMaps/EntityMap";

@Component({
    selector: 'rd-edit-property',
    templateUrl: 'edit-property.component.html'
})
export class EditPropertyComponent implements OnInit {

    @Input() property: Property;
    @Input() entity: Entity;
    @Input() item:any={};

    constructor(
	    private entityMap: EntityMap) {
    }

    ngOnInit() {
    }

    public get propertyValue(){
        return this.item[this.property.name.name] ;
    }
    
    public set propertyValue(v : string) {
        this.item[this.property.name.name]= v;
    }
    
}


