import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HomePage } from '../pages/home/home';
import { ListPage } from '../pages/list/list';
import { EntityListPage } from "../pages/entityList/entity-list";
import { EntityMap } from "../EntityMaps/EntityMap";
import { EntityDetailsPage } from "../pages/entityDetails/entity-details";


class PageData {
  title: string;
  component: any;
  parameters?: any;
}

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav)
  nav: Nav;

  rootPage: any = HomePage;

  pages: Array<PageData>;

  constructor(public platform: Platform,
    public statusBar: StatusBar,
    public splashScreen: SplashScreen,
    public entityMap: EntityMap
  ) {
    this.initializeApp();

    // used for an example of ngFor and navigation
    this.pages = [
      { title: 'Home', component: HomePage },
      { title: 'List', component: ListPage },
      { title: 'Entity List', component: EntityListPage, parameters: { app: this.entityMap.app.name } },
      { title: 'Entity Details', component: EntityDetailsPage }
    ];
  }

  initializeApp() {
    this.platform.ready().then(this.init);
  }
  private init = () => {
    // Okay, so the platform is ready and our plugins are available.
    // Here you can do any higher level native things you might need.
    this.entityMap.loadEntities();
    this.statusBar.styleDefault();
    this.splashScreen.hide();
    return "";
  }
  openPage(page: PageData) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component, page.parameters);
  }

}



