import { Component } from '@angular/core';
import { Response, } from '@angular/http';

import { NavController, NavParams, LoadingController } from 'ionic-angular';


import { EntityMap } from "../../EntityMaps/EntityMap";
import { Entity } from "../../EntityMaps/Entity";
import { DataService } from "../../Services/data.service";

@Component({
  selector: 'entity-details',
  templateUrl: 'entity-details.html'
})
export class EntityDetailsPage {
  entity: Entity;
  item: any;
  entityName: string;
  itemId: number;
  constructor(
    private dataService: DataService,
    public navCtrl: NavController,
    public navParams: NavParams,
    private entityMap: EntityMap,
    public loadingCtrl: LoadingController
  ) {
    // If we navigated to this page, we will have an item available as a nav param
    this.item = navParams.get('item');
    this.entity = navParams.get('entity');
    if (!this.item) {
      this.entityName = navParams.get('entityName');
      this.itemId = <number>navParams.get('itemId');
      this.getItem();
    }
  }

  itemTapped = (event, item) => {
    // That's right, we're pushing to ourselves!

  };

  getItem = () => {
    if (this.entityName) {
      this.entity = this.entityMap.getEntity(this.entityName);
      if (this.entity) {
        this.getItemDetails();
      } else {
        this.entityMap.addCompletedSubscribers(this.downloadEntityMapCompleted);

      }
    }
    return;
  };

  getItemDetails = () => {
    var observable = this.dataService.getEntity(this.entity, this.itemId);

    observable.map((res: Response) => {
      return res.json();
    })
      .subscribe(this.acceptData,
      this.downloadItemDetailsFailed,
      this.downloadComplete
      );
    return;
  };

  downloadEntityMapCompleted = () => {
    this.entity = this.entityMap.getEntity(this.entityName);
    this.getItemDetails();
  };
  downloadItemDetailsFailed = (err: any) => {
    console.log("EntityDetailsPage.downloadItemDetailsFailed");
    console.log(err);
  };
  acceptData = (data: any) => {
    console.log("EntityDetailsPage.acceptData:" + data);
    this.item = data;
  };

  downloadComplete = () => {
  };


}



