import { Component, Input, OnInit } from '@angular/core';
import {EntityMap} from "../EntityMaps/EntityMap";
import {TableColumnVm} from "../ViewModels/TableColumnVm";
import {PropertyType} from "../EntityMaps/PropertyType";

@Component({
    selector: 'rd-search-property',
    templateUrl: 'search-property.component.html',
    styleUrls: ['']
	
})
export class SearchPropertyComponent implements OnInit {

	@Input() column: TableColumnVm;
	sub: any;

    constructor(
		private entityMap: EntityMap) {
    }

    ngOnInit() {
	    if (!this.column.table.filterData) {
			this.column.table.filterData = {};
		}
		let type = this.column.property.type;
		let filter = this.column.table.filterData;

		if (type === PropertyType.Number || type === PropertyType.Date) {
			let propertyName = this.column.property.name.name;
			if (!filter[propertyName]) {
				filter[propertyName] = {};
			}
		}
		
    }

}


