import { Injectable } from '@angular/core';
import { Entity } from "../../EntityMaps/Entity";
import { ODataFilterService } from "./ODataFilterService";
import { ParentNameOdataParameterBuilder } from "./ParentNameOdataParameterBuilder";
import { LinkModeODataParameterBuilder } from "./LinkModeODataParameterBuilder";
import {TableRequest} from "../../ResponsiveTable/table-request";
import {SortDirection} from "../../ResponsiveTable/SortDirection";

@Injectable()
export class ODataUrlBuilder {
    constructor(
        private odataFilterService: ODataFilterService,
        private linkModeODataParameterBuilder: LinkModeODataParameterBuilder,
        private parentNameOdataParameterBuilder: ParentNameOdataParameterBuilder
    ) { }
    buildListUrl = (entity: Entity, options: TableRequest) => {
        let listUrl = `odata/${entity.pluralName}/?$count=true`;
        if (options) {
            if (!options.limit) {
                options.limit = 10;
            }
            if (options.limit && options.limit !== 0) {
                listUrl += `&$top=` + options.limit;
            }

            if (!options.offset) {
                options.offset = 0;
            }
            listUrl += `&$skip=` + options.offset;

            if (options.sortColumn) {
                listUrl += `&$orderby=` + options.sortColumn;
                if (options.sortDirection === SortDirection.Desc) {
                    listUrl += ' desc';
                }
            }



            let expandParameters = this.parentNameOdataParameterBuilder.getContentParameters(entity, options);

            if (expandParameters) {
                listUrl += expandParameters;
            }
        }
        return listUrl;
    };

    

    buildDeleteUrl = (entityPluralName: string, id: number) => {
        let deleteUrl = `odata/${entityPluralName}(${id})`;
        return deleteUrl;
    };

    buildPatchUrl = (entityPluralName: string, id: number) => {
        let deleteUrl = `odata/${entityPluralName}(${id})`;
        return deleteUrl;
    };

    buildPostUrl = (entity: Entity) => {
        let deleteUrl = `odata/${entity.pluralName}`;
        return deleteUrl;
    };

    buildGetUrl = (entityPluralName: string, id: number) => {
        let deleteUrl = `odata/${entityPluralName}/?key=` + id;;
        return deleteUrl;
    };
}


