import { Component, EventEmitter, Input, Output, OnInit } from '@angular/core';
declare var jQuery: any;
@Component({
})
export class ModalDialogService implements OnInit {
	@Input() title: string;
	@Input() message: string;

	@Output() eventName = new EventEmitter();

	constructor() { }

	ngOnInit() { }

	open = (title: string, message: string, iconClass: string = "glyphicon glyphicon-info-sign") => {
		const template = `<div class="modal fade" id="myModal" role="dialog">
	<div class="modal-dialog">
		<!-- Modal content-->
		<div class="modal-content">
			<div class="modal-header">
				<button type="button" class="close" data-dismiss="modal">&times;</button>
				<h4><span class="${iconClass}"></span>${title}</h4>
			</div>
			<div class="modal-body">
				<div class="form-group">
					<div>${message}</div>
					<div class="modal-footer">
						<button type="submit" class="btn btn-default btn-default pull-left" data-dismiss="modal"><span class="glyphicon glyphicon-remove"></span> Cancel</button>
					</div>
				</div>
			</div> 
		</div>
	</div>
</div>`;
		jQuery(template).modal('show');
	};
	openId = (id: string) => {
		jQuery("#" + id).modal('show');
	}
	closeId = (id: string) => {
		jQuery("#" + id).modal('hide');
	}
	showErrorDialog = (title: string, message: string) => {
		this.open(title, message, "fa fa-exclamation-triangle");
	};
}


