import * as TimeOffsetFormatter from "./IDateTimeOffsetFormatter";

export class DateTimeOffsetFormatter implements TimeOffsetFormatter.IDateTimeOffsetFormatter {
    formatLocalDate = (dateString: string) => {
        let now = new Date(dateString);
        if (dateString === null || now.toString() === "Invalid Date") {
            throw new Error("Invalid Date");
        }
        let timezoneOffset = -now.getTimezoneOffset();
        let dif = timezoneOffset >= 0 ? '%2B' : '-';//%2B is character '+', it's not allowed in URL, so encoding it

        return now.getFullYear()
            + '-' + this.pad(now.getMonth() + 1)
            + '-' + this.pad(now.getDate())
            + 'T' + this.pad(now.getHours())
            + ':' + this.pad(now.getMinutes())
            + ':' + this.pad(now.getSeconds())
            + dif + this.pad(timezoneOffset / 60)
            + ':' + this.pad(timezoneOffset % 60);
    }
    private pad = (num: number) => {
        var norm = Math.abs(Math.floor(num));
        return (norm < 10 ? '0' : '') + norm;
    };
}


