import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Response } from '@angular/http';
import { PropertyType } from "../EntityMaps/PropertyType";
import { TableViewMode, TableVm } from "../ViewModels/TableVm";
import { RelationProperty } from "../EntityMaps/RelationProperty";
import { EntityMap } from "../EntityMaps/EntityMap";
import { ModalDialogService } from "../Services/modal-dialog.service";
import { RelationService } from "../Services/RelationService";
import { TableColumnVm } from "../ViewModels/TableColumnVm";
import { AlertController } from 'ionic-angular';
import {GuidService} from "../Commons/GuidService";
import {TableDataMode} from "./table-request";

@Component({
    selector: 'rd-table',
    templateUrl: 'table.component.html',
    providers: [ModalDialogService]
})
export class TableComponent implements OnInit, OnDestroy {
    error: any;
    @Input() table: TableVm;
    @Input() parentProperty: RelationProperty;
    @Input() parentId: number;
    addUrl: string;
    originalFilterData: any;
    constructor(
        private entityMap: EntityMap,
        private relationService: RelationService,
        private alertCtrl: AlertController,
        private guidService: GuidService
    ) {
        this.getTableSearchFormId();
    }

    ngOnInit() {
        this.table.downloadItems();
        this.updateAddUrl();
    }

    ngOnDestroy() {
    }

    sort = (column: TableColumnVm) => {
        this.table.sortColumn = column;
        this.table.downloadItems();
    };

    search = () => {
        this.table.downloadItems();
        //this.modalDialogService.closeId(this.searchFormId);
    };

    clearSearch = () => {
        this.table.keyword = '';
        this.search();
    };

    onPageSelected(pageNumber: number) {
        this.table.currentPage = pageNumber;
        this.table.downloadItems();
    }
    showDeleteConfirm(item:any) {
        let confirm = this.alertCtrl.create({
            title: 'Are you sure?',
            message: 'Do you want to delete this?',
            buttons: [
                {
                    text: 'Cancel',
                    handler: () => {
                        console.log('Disagree clicked');
                    }
                },
                {
                    text: 'Yes',
                    handler: () => {
                        this.table.delete(item)
                            .map((res: Response) => {
                                return res.json();
                            })
                            .subscribe(this.getDeleteResult,
                            this.errorOnDelete,
                            this.deleteComplete
                            );
                    }
                }
            ]
        });
        confirm.present();
    }
    deleteConfirmed = (item: any) => {
        this.table.delete(item)
            .map((res: Response) => {
                return res.json();
            })
            .subscribe(this.getDeleteResult,
            this.errorOnDelete,
            this.deleteComplete
            );
    };

    searchFormId: string;

    getTableSearchFormId = () => {
        if (!this.searchFormId) {
          this.searchFormId = "searchDialogId_" + this.guidService.guid();
        }
        return this.searchFormId;
    }

    addNewItem = () => {
        const baseUrl = this.table.entity.name.name + '/add';

        let parentPropertyName = this.getParentThatEndPropertyName();
        let url: string = '';
        if (parentPropertyName) {
            url = baseUrl + "/" + parentPropertyName + '/' + this.parentId;
        } else {
            url = baseUrl;
        }
        //TODO: 
        //this.router.navigate([url]);
    };

    private getParentThatEndPropertyName = () => {
        let parentPropertyName: string;
        if (this.parentProperty) {
            parentPropertyName = this.parentProperty.thatEndPropertyName;
        } else {
            parentPropertyName = null;
        }
        return parentPropertyName;
    };

    public isLinkItemButtonVisible = (): boolean => {
        return this.table.options.tableDataMode === TableDataMode.Normal && this.parentProperty !== null && this.parentProperty !== undefined;
    };

    public isFinishLinkItemButtonVisible = (): boolean => {
        return this.table.options.tableDataMode === TableDataMode.Link && this.parentProperty !== null && this.parentProperty !== undefined;
    };

    linkItem = () => {
        if (this.isLinkItemButtonVisible()) {
            this.table.options.tableDataMode = TableDataMode.Link;
            this.table.options.parentId = this.parentId;
            this.table.options.parentProperty = this.parentProperty.thatEndPropertyName;
            this.table.currentPage = 1;
            this.originalFilterData = this.table.filterData;
            this.table.filterData = this.getEmptyFilter();
            this.table.refresh();
        }
    };
    getEmptyFilter = () => {
        let emptyFilterData = {};

        for (var column of this.table.columns) {
            if (column.property.type === PropertyType.Number || column.property.type === PropertyType.Date) {
                emptyFilterData[column.property.name.name] = {};
            }
        }
        return emptyFilterData;
    }
    finishLinkItem = () => {
        if (this.isFinishLinkItemButtonVisible()) {
            this.table.options.tableDataMode = TableDataMode.Normal;
            this.table.currentPage = 1;
            this.table.options.parentProperty = this.parentProperty.thatEndPropertyName;
            this.table.filterData = this.originalFilterData;
            this.table.refresh();
        }
    };
    link = (item: any) => {
        this.table.link(item, this.parentProperty.thatEndPropertyName, this.parentId)
            .map((res: Response) => {
                return res.json();
            })
            .subscribe(this.getLinkResult,
            this.errorOnLink,
            this.linkComplete
            );
    }

    unlink = (item: any) => {
        this.table.unlink(item, this.parentProperty.thatEndPropertyName, this.parentId)
            .map((res: Response) => {
                return res.json();
            })
            .subscribe(this.getUnlinkResult,
            this.errorOnUnlink,
            this.unlinkComplete
            );
    }
    isSelected = (item: any) => {
        let isNtoN = this.relationService.isNtoNRelation(this.parentProperty);
        if (isNtoN) {
            let children = item[this.parentProperty.thatEndPropertyName];
            return children && children.length > 0;
        } else {
            return item[this.parentProperty.thatEndPropertyName] === this.parentId;
        }
    }
    updateAddUrl = () => {
        this.addUrl = this.table.entity.name.name + '/add';

        if (this.parentProperty) {
            this.addUrl += `?${this.parentProperty.thatEndEntityName}=${this.parentId}`;
        }
    };
    errorOnDelete = (err: any) => {
        let alert = this.alertCtrl.create({
            title: 'Error!',
            subTitle: 'Delete failed!',
            buttons: ['OK']
        });
        alert.present();
    };

    errorOnLink = (err: any) => {
        //this.modalDialogService.open("Error", "Failed to link record");
    };
    errorOnUnlink = (err: any) => {
        //this.modalDialogService.open("Error", "Failed to unlink record");
    };
    getDeleteResult = (data: any) => {

    };
    getLinkResult = (data: any) => {

    };
    getUnlinkResult = (data: any) => {

    };

    deleteComplete = () => {
        this.table.downloadItems();
    };

    linkComplete = () => {
        this.table.downloadItems();
    };

    unlinkComplete = () => {
        this.table.downloadItems();
    };

    viewRegularTable = () => {
        this.table.viewMode = TableViewMode.Regular;
    }

    viewStackTable = () => {
        this.table.viewMode = TableViewMode.Stack;
    }
}


