import { Injectable } from '@angular/core';

import * as DataFilterBuilderFactory from "./ODataFilterBuilderFactory";
import {Entity} from "../../EntityMaps/Entity";
import {TableRequest} from "../../ResponsiveTable/table-request";

@Injectable()
export class ODataFilterService {
    constructor(private filterBuilderFactory: DataFilterBuilderFactory.ODataFilterBuilderFactory) {

    }
    getOdataFilter = (entity: Entity,
        options: TableRequest
    ) => {
        let filterStr = '';

        if (options.filterData) {
            let filterNames = Object.keys(options.filterData);
            for (var filterName of filterNames) {
                let filterForProperty = this.getFilterForProperty(entity, options, filterName);
                if (filterStr && filterForProperty) {
                    filterStr += " and ";
                }
                filterStr += filterForProperty;
            }
            if (filterStr) {
                filterStr = '&$filter=' + filterStr;
            }
        }
        return filterStr;
    }

    getFilterForProperty = (entity: Entity, options: TableRequest, filterName: string): string => {
        let property = entity.properties.find(p => p.name.name === filterName);
        let propertyType = property.type;
        let filterBuilder = this.filterBuilderFactory.createFilterBuilder(propertyType);
        return filterBuilder.getFilter(options.filterData, property);
    }
}


