import { Component, EventEmitter, Input, Output, OnInit, ChangeDetectorRef } from '@angular/core';
import { Response } from '@angular/http';
import {
    FormBuilder,
    FormGroup } from '@angular/forms';
import { TableRequest } from "./table-request";
import { SortDirection } from "./SortDirection";
import { TableVm } from "../ViewModels/TableVm";
import { ModalDialogService } from "../Services/modal-dialog.service";
import { Entity } from "../EntityMaps/Entity";
import { DataService } from "../Services/data.service";
import { EntityMap } from "../EntityMaps/EntityMap";
import { RelationService } from "../Services/RelationService";
import { TableRequestService } from "../ViewModels/TableRequestService";
import { IProperty } from "../EntityMaps/IProperty";
import { PropertyType } from "../EntityMaps/PropertyType";
import { RelationProperty } from "../EntityMaps/RelationProperty";
import { FormControlService } from "./FormControlService";
declare var jQuery: any;

@Component({
    selector: 'rd-edit-entity',
    templateUrl: 'edit-entity.component.html',
    providers: [ModalDialogService]
})
export class EditEntityComponent implements OnInit {
    sub: any;
    @Input() entity: Entity;
    @Input() item: any = {};
    @Output() eventName = new EventEmitter();
    form: FormGroup;
    parents = {
    };

    private parentId: number;
    private parentPropertyName: string;
    private parentSelectionTable: { [id: string]: TableVm; } = {};
    constructor(
        private dataService: DataService,
        private entityMap: EntityMap,
        private cdr: ChangeDetectorRef,
        private fb: FormBuilder,
        private dialogService: ModalDialogService,
        private relationService: RelationService,
        private tableRequestService: TableRequestService,
        private formControlService: FormControlService
    ) {
    }

    public hasBaseDropZoneOver: boolean = false;
    public hasAnotherDropZoneOver: boolean = false;

    public fileOverBase(e: any): void {
        this.hasBaseDropZoneOver = e;
    }

    public fileOverAnother(e: any): void {
        this.hasAnotherDropZoneOver = e;
    }
    ngOnInit() {
        //TODO: download parent
        //     this.sub = this.route.params.subscribe((params: any) => {
        //const entityNameParam = 'entityName';
        //const idParam = 'id';
        //const parentPropertyNameParam = 'parentPropertyName';
        //const parentIdParam = 'parentId';
        //this.parentId = params[parentIdParam];
        //this.parentPropertyName = params[parentPropertyNameParam];


        //         if (params[entityNameParam] !== undefined) {
        //             let entityName = params[entityNameParam];
        //	let id = params[idParam];

        //             this.entity = this.entityMap.getEntity(entityName);
        //	this.uploadManager.setupFileUploader(this.entity,this.item);
        //	this.createFormControls();
        //	this.downloadEntity(this.entity, id);
        //	this.downloadParents();
        //         } 
        //     });
        this.cdr.detectChanges();
        this.createDropDownListOnChangeHandlers();
        this.updateParent();

    }
    createTableVmListForParentProperties = () => {
        for (var property of this.entity.properties) {
            this.parentSelectionTable[property.name.name] = new TableVm(this.dataService, this.entity, this.relationService, this.tableRequestService);
        }
    }
    getUploader = (property: IProperty) => {
        //this.uploadManager.getUploader(property);
    };

    createGuid() {
        function f() {
            return Math.floor((1 + Math.random()) * 0x10000).toString(16).substring(1);
        }
        return f() + f() + '-' + f() + '-' + f() + '-' + f() + '-' + f() + f() + f();
    }
    downloadEntity = (entity: Entity, id: number) => {
        if (id) {
            this.dataService.getEntity(this.entity, id)
                .map((res: Response) => {
                    return res.json();
                })
                .subscribe(this.acceptData,
                (err: any) => console.log('downloadEntity() error: ' + err),
                this.downloadComplete
                );
        }

    };


    is1ToNProperty(property: IProperty) {
        return property.type === PropertyType.Relation &&
            this.relationService.is1toNRelation((<RelationProperty>property));
    }
    isManyToOneProperty(property: IProperty) {
        return property.type === PropertyType.Relation &&
            this.relationService.isManyToOneProperty((<RelationProperty>property));
    }

    isOneToManyOrManyToManyProperty(property: IProperty) {
        return property.type === PropertyType.Relation &&
            this.relationService.isOneToManyOrManyToManyProperty((<RelationProperty>property));
    }

    downloadParents = () => {
        for (var property of this.entity.properties) {
            if (property.type === PropertyType.Relation) {
                let parentProperty = <RelationProperty>property;
                let isManyToOne = this.relationService.isManyToOneProperty(parentProperty);
                if (isManyToOne) {
                    let parentEntity = this.entityMap.getEntity(parentProperty.entityName.name);

                    let options: TableRequest = new TableRequest();
                    options.limit = -1;
                    options.offset = 0;
                    options.sortDirection = SortDirection.Asc;
                    options.sortColumn = parentProperty.displayPropertyName.name;

                    let parentColumnName = parentProperty.name.name;
                    this.dataService.getEntitySummaries(parentEntity, options)
                        .map((res: Response) => {
                            return res.json();
                        })
                        .subscribe((data: any) => {
                            let items: any[] = [];
                            for (var row of data.rows) {
                                let parentItem: any = {};
                                parentItem.id = row[parentEntity.idColumn.name.name];
                                parentItem.text = row[parentProperty.displayPropertyName.name];
                                items.push(parentItem);
                            }
                            this.parents[parentColumnName] = items;
                            this.updateDropDownLists();

                        },
                        (err: any) => console.log('downloadParents() error:' + err),
                        this.downloadComplete
                        );
                }

            }
        }
    };
    getKeys(obj: any) {
        var keys: any[] = [];
        for (var key in obj) {
            keys.push(key);
        }
        return keys;
    }
    hasPropertyError(property: IProperty) {
        if (this.form.controls[property.name.name]) {
            return !this.form.controls[property.name.name].valid;
        }
        return false;
    }
    private dropDownOnChangeHandlers = {}
    createDropDownListOnChangeHandlers = () => {
        for (var property of this.entity.properties) {
            if (property.type === PropertyType.Relation) {
                let changedProperty = property;
                this.dropDownOnChangeHandlers[property.name.name] = (e: any) => {
                    let value = jQuery(e.target).val();
                    this.selectionChanged(changedProperty, value);
                };
            }
        }
    };
    updateParent = () => {
        for (var property of this.entity.properties) {
            if (property.type === PropertyType.Relation) {
                if (property.name.name === this.parentPropertyName) {
                    this.item[property.name.name] = this.parentId;
                }
            }
        }
    }
    updateDropDownLists = () => {
        for (var property of this.entity.properties) {
            if (property.type === PropertyType.Relation) {
                const selector = ".select_" + property.name.name;
                const change = "change";
                const handler = this.dropDownOnChangeHandlers[property.name.name];
                jQuery(selector).off(change, handler);
                jQuery(selector).on(change, handler);
            }
        }
    };
    selectionChanged = (property: IProperty, newValue: any) => {
        this.item[property.name.name] = newValue;
    };
    showSelectParentDialog = (property: IProperty, item: any) => {
        jQuery("#parentModel_" + property.name.name).modal("show");
        console.log("showSelectParentDialog" + property.name + "->" + JSON.stringify(item));
    }
    private createFormControls = () => {
        let controls = this.formControlService.createFormControlsForProperties(this.entity.properties);
        this.form = this.fb.group(controls);
    };

    acceptData = (data: any) => {
        this.item = data;
        for (var property of this.entity.properties) {
            if (property.type === PropertyType.Date) {
                if (data[property.name.name]) {
                    //TODO
                    let date = new Date(data[property.name.name]);
                    //let newDataFormat = moment(date.getTime()).format("YYYY-MM-DD");
                    //data[property.name.name] = newDataFormat;
                }
            }
        }
        //TODO
        //this.uploadManager.setupFileUploader(this.entity, this.item);
        this.setupUploaderFilenames();
        this.item = data;
    };
    setupUploaderFilenames = () => {
        for (var property of this.entity.properties) {
            if (property.type === PropertyType.Image || property.type === PropertyType.File) {
                //this.uploadManager.setFilename(this.entity, property, this.itemId.toString(), this.item[property.name.name]);
            }
        }
    };
    downloadComplete = () => {
        //TODO
        //this.uploadManager.setupFileUploader(this.entity, this.item);
        this.cdr.detectChanges();
    };

    saveComplete = () => {
        console.log("saveComplete");
    };

    acceptSubmitResult = (result: any) => {
        this.itemId = result.value;
        //this.router.navigateByUrl(this.detailsUrl);
    };
    isAdd: boolean;
    submitEntity = () => {
        this.isAdd = !this.hasId();
        this.dataService.save(this.entity, this.item)
            .map((res: Response) => {
                return res.json();
            })
            .subscribe(this.acceptSubmitResult,
            this.processSaveError,
            this.saveComplete
            );
    };

    processSaveError = (err: any) => {
        this.dialogService.showErrorDialog("Error", "Failed to save data");
    };
    get detailsUrl() {
        return '/' + this.entity.name.name + "/view/" + this.itemId;
    }
    isFormValid = () => {

    };
    get itemId() {
        let id = this.item[this.entity.idColumn.name.name];
        return id ? id : '';
    }
    set itemId(id: number) {
        this.item[this.entity.idColumn.name.name] = id;
    }
    hasId() {
        if (this.itemId) {
            return false;
        } else {
            return true;
        }
    }
    hasError = (property: IProperty, errorKey: string) => {
        let control = this.controlOf(property);
        return control ? control.hasError(errorKey) : null;
    };
    isTouched = (property: IProperty) => {
        return this.controlOf(property).touched;
    }
    controlOf = (property: IProperty) => {
        return this.form.controls[property.name.name];
    }
}


